@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*::after,*::before{box-sizing:inherit}.app{height:100vh;display:grid;grid-template-rows:repeat(3, 1fr);grid-template-columns:1fr;justify-items:center;align-items:center}.app__title{font-size:26px;text-align:center}.app__update{text-align:center}.app__update-button{border:none;background-color:#c0bebe;width:165px;height:40px;border-radius:30px;font-size:1.2rem;font-weight:600;box-shadow:0 0 0 7px #c0bebe inset}.app__update-button:hover,.app__update-button:focus{background:#a1a0a0;cursor:pointer}@media (min-width: 550px) and (max-width: 996px){.app__title{font-size:2em}.app__update-button{width:260px;height:70px;font-size:1.5rem}}@media (min-width: 996px){.app__title{font-size:2.2em}.app__update-button{width:240px;height:70px;font-size:1.5rem}}

